import { _decorator, Component,assert,AudioSource, Node, Label, EditBox,sys, director, Button,game, EventHandler, ToggleContainerComponent, ToggleComponent, Sprite } from 'cc';
const {ccclass, property} = _decorator;



import { AudioManager } from "../common/AudioManager";

@ccclass('Profile')
export default class Profile extends Component {

    @property(ToggleComponent)
    rdMale: ToggleComponent = null;
  
    @property(ToggleComponent)
    rdFemale: ToggleComponent = null;


    @property(Node)
    imgFemale:Node  = null;

    @property(Node)
    imgMale:Node  = null;
 

  onLoad() {

    const containerEventHandler = new EventHandler();
    // This Node is the node to which your event processing code component belongs
    containerEventHandler.target = this.node;
    // This is the script class name
    containerEventHandler.component = 'example';
    containerEventHandler.handler = 'callback';
    containerEventHandler.customEventData = 'foobar';
    const container = this.node.getComponent(ToggleContainerComponent);
    container.checkEvents.push(containerEventHandler);

  }

  callback(event: Event, customEventData: string){
    // The event here is a Touch Event object, and you can get the send node of the event by event.target
    // The customEventData parameter here is equal to the "foobar" you set before
      if(this.rdMale.isChecked===true){
         this.imgMale.active=true;
         this.imgFemale.active=false;

      }else{
        this.imgMale.active=false;
        this.imgFemale.active=true;


      }
   
}

   
   exitScreen(){

    if(AudioManager.isMusicOn()){
      AudioManager.pauseMusic()
    
      
  }else{
    AudioManager.playMusic();
    AudioManager.playSound("click");      

  }

   }
  
  start() {



    // AudioManager.setStateMusic("1");
    //          AudioManager.checkPlayMusic();

  }

  update(deltaTime: number) {
        
}


}

